.main {
    display: flex;
    flex-direction: column;
    /* gap: 50px; */
    width: 100%;
    padding: 0;
    box-sizing: border-box;
    gap: 10px;
}

.main__vertical-stripe-wrapper {
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.main__vertical-stripe {
    background: repeating-linear-gradient(90deg, transparent, transparent 8px, rgba(255, 255, 255, .4) 9px, rgba(255, 255, 255, .4) 11px);
    width: 33px;
    min-width: 33px;
}

.main__title {
    display: flex;
    flex-direction: column;
    gap: 24px;
    box-sizing: border-box;
    width: 100%;
    justify-content: space-between;
}

.main__title-heading {
    color: white;
    font-family: 'Haas', sans-serif;
    font-size: 72px;
    line-height: 0.82;
    margin: 0;
    text-transform: lowercase;
    text-align: start;
    padding-top: 50px;
}

.main__title-letters {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
}

.main__title-heading-letter {
    color: white;
    opacity: .25;
    font-family: 'Haas', sans-serif;
    font-size: 120px;
    line-height: 1.1em;
    margin: 0;
    text-transform: uppercase;
}

.main__footer {
    color: gray;
    font-family: 'Inter', sans-serif;
    font-size: 12px;
    line-height: 16px;
    margin: 0;
    padding-bottom: 10px;
    align-self: center;
}

.main__footer-link {
    color: gray;
    cursor: pointer;
    opacity: 1;
    transition: opacity .15s ease-in;
}

.main__footer-link:hover {
    opacity: 0.7;
}

.main__grid {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(var(--columns), calc(100cqi/var(--columns) - var(--gutter-size)*var(--gutters)/var(--columns)));
    grid-template-rows: repeat(var(--columns) - calc(100cqi/var(--columns) - var(--gutter-size)*var(--gutters)/var(--columns)));
    gap: 10px;
    box-sizing: border-box;
    padding: 10px 0;
}

.main__grid-link {
    /* background: repeating-linear-gradient(135deg, transparent, transparent 10px, white 5px, white 12px); */
    /* background: repeating-linear-gradient(135deg, transparent, transparent 15px, rgba(255, 255, 255, .33) 15px, rgba(255, 255, 255, .33) 16px); */
    line-height: 0;
    position: relative;
}

.main__grid-img {
    /* filter: contrast(200%) saturate(0%); */
    object-fit: cover;
    width: 100%;
    height: 100%;
    filter: saturate(0%);
    transition: filter .15s ease-in;
    /* mix-blend-mode: multiply; */
    cursor: pointer;
    /* box-shadow:
    0px 0px 6.8px rgba(0, 0, 0, 0.23),
    0px 0px 18.8px rgba(0, 0, 0, 0.33),
    0px 0px 45.2px rgba(0, 0, 0, 0.43),
    0px 0px 150px rgba(0, 0, 0, 0.66); */
}

.main__grid-link:hover > .main__grid-img {
    filter: saturate(100%) brightness(110%);
}

.main__grid-link:hover > .main__grid-img_husband-02 {
    filter: saturate(100%) brightness(95%);
}

.main__grid-link:hover > .main__grid-img_husband-01 {
    filter: saturate(100%) contrast(100%) brightness(95%);
}

.main__grid-img-number-wrapper {
    bottom: 0px;
    right: 0px;
    width: 0;
    height: 0;
    border-color: transparent black black transparent;
    border-width: 20px;
    border-style: solid;
    text-decoration: none;
    color: gray;
    position: absolute;
    /* background-color: black; */
    font-family: 'Haas', sans-serif;
    font-size: 18px;
    line-height: 14px;
    /* height: 22px; */
    /* width: 32px; */
    display: flex;
    justify-content: end;
    align-items: end;
    /* border-start-start-radius: 20px; */
    transition: color .15s ease-in;
}

.main__grid-img-number {
    text-decoration: none;
    color: gray;
    font-family: 'Haas', sans-serif;
    font-size: 18px;
    line-height: 14px;
    transition: color .15s ease-in;
    bottom: -20px;
    position: absolute;
    right: -20px;
}

.main__grid-link:hover > .main__grid-img-number-wrapper > .main__grid-img-number {
    color: white;
}

.main__grid_sanada-01 {
    grid-row: 5 / 6;
    grid-column: 4 / 5;
}

.main__grid_sanada-02 {
    grid-row: 1 / 3;
    grid-column: 1 / 3;
}

.main__grid_sanada-03 {
    grid-row: 3 / 5;
    grid-column: 1 / 2;
}

.main__grid_sanada-03 > .main__grid-img {
    width: 100%;
    height: 100%;
}

.main__grid_sanada-05 {
    grid-row: 1 / 2;
    grid-column: 3 / 5;
    aspect-ratio: 2 / 1;
}

.main__grid_hairstyles-01 {
    grid-row: 3 / 5;
    grid-column: 3 / 5;
    aspect-ratio: 1;
}

/* .main__grid_hairstyles-04 {
    grid-row: 1 / 2;
    grid-column: 3 / 5;
    aspect-ratio: 2 / 1;
} */

/* .main__grid_hairstyles-05 {
    grid-row: 5 / 6;
    grid-column: 4 / 5;
} */

.main__grid_husband-01 {
    grid-row: 4 / 5;
    grid-column: 2 / 3;
    aspect-ratio: 1;
}

.main__grid-img_husband-01 {
    filter: saturate(0) contrast(85%) brightness(85%);
    transition: filter .15s ease-in;
}

.main__grid_husband-02 {
    grid-row: 2 / 3;
    grid-column: 3 / 4;
    aspect-ratio: 1;
}

.main__grid-img_husband-02 {
    filter: saturate(0%) brightness(75%);
    transition: filter .15s ease-in;
}

.main__grid_blank-01 {
    aspect-ratio: 1;
    /* background: repeating-linear-gradient(45deg, rgba(255, 255, 255, .5) 1px, transparent 1px, transparent 14px, rgba(255, 255, 255, .5) 15px); */
}

.main__grid_blank-02 {
    /* background: repeating-linear-gradient(135deg, rgba(255, 255, 255, .5) 1px, transparent 1px, transparent 14px, rgba(255, 255, 255, .5) 15px); */
    aspect-ratio: 1;
}

.main__grid_facial-anatomy-03 {
    grid-row: 5 / 6;
    grid-column: 1 / 3;
    aspect-ratio: 2 / 1;
}

.main__grid_husband-01:hover {
    filter: none;
}

.main__grid-link:active > .main__grid-img {
    filter: contrast(125%);
    transition: filter .05s ease-in;
}

.main__grid-img_contrast:hover {
    filter: none;
}

.main__grid-img_contrast:active {
    filter: contrast(200%) saturate(0%);
    transition: filter .05s ease-in;
}

@media screen and (max-width: 1024px) {
    .main__title {

    }

    .main__vertical-stripe {
        display: none;
    }

    .main {
        padding: 0 4vw 4vw 4vw;
    }

    .main__title-heading {
        padding: 4vw 0 0 0;
        font-size: 12vw;
    }

}

@media screen and (max-width: 768px) {
    .main__grid {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(9, 1fr);
    }

    .main__grid_blank-02 {
        display: none;
    }

    .main__grid_sanada-01 {
        grid-row: unset;
        grid-column: unset;
    }
    
    .main__grid_sanada-02 {
        grid-column: 1 / 3;
        grid-row: 2 / 4;
    }
    
    .main__grid_sanada-03 {
        grid-row: 4/6;
        grid-column: 1/2;
    }
    
    .main__grid_hairstyles-01 {
        grid-column: 1 / 3;
        grid-row: 6 / 8;
        aspect-ratio: auto;
    }
    
    .main__grid_sanada-05 {
        grid-row: 1 / 2;
        grid-column: 1 / 3;
        aspect-ratio: 2 / 1;
    }
    
    .main__grid_husband-01 {
        aspect-ratio: auto;
        grid-row: unset;
        grid-column: unset;
    }
    
    .main__grid_husband-02 {
        aspect-ratio: auto;
        grid-row: unset;
        grid-column: unset;
    }
    
    .main__grid_facial-anatomy-03 {
        grid-column: 1 / 3;
        aspect-ratio: 2 / 1;
        grid-row: 9 / 10;
    }
}